/* src/components/HeroSection.css */
body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-image: url('../Asset/developer.jpg'); 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    /* couleur de fond sombre */
    color: #fff; 
    /* font-family: 'Space Mono', monospace; couleur du texte */
    padding: 50px;
    
   
    
}

.content {
    max-width: 600px;
}

.greeting span {
    background-color: #18e06e;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 18px;
}

h1 {
    font-size: 48px;
    font-weight: bold;
    line-height: 1.2;
}

h1 span {
    color: #18e06e; /* couleur verte */
}

p {
    font-size: 18px;
    margin-top: 20px;
    line-height: 1.6;
}

